* {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
  }
  html {
    scroll-behavior: smooth;
  }
  .wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 10px;
  }
  .main-content {
    padding: 72px 0 0 0;
    flex: 1;
  }
  .container {
    margin: 0 auto;
    max-width: 1120px;
  }
  button,
  a {
    cursor: pointer;
    border: none;
    text-decoration: none;
  }
  h1 {
    font-size: 56px;
    font-weight: 700;
    line-height: 80px;
  }
  h2 {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 44px;
  }
  h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
  }
  :root {
    --primary: #2231aa;
    --primary-2: #5360cd;
    --error: #fd3419;
    --secondary: #313037;
  }
  @media (max-width: 540px) {
    h1 {
      font-size: 32px;
      line-height: 48px;
    }
    h2 {
      font-size: 24px;
      line-height: 36px;
    }
    .main-content {
      padding: 56px 0 0 0;
    }
  }
  